import QQMapWX from '@/common/utils/qqmap-wx-jssdk.min.js'
import tuiIcon from "@/components/icon/icon"
export default {
	components: {
		tuiIcon
	},
	data() {
		return {
			inputShowed: false,
			inputVal: "",
			amapPlugin: null,
			city: '',
			address: [],
			scrollH: 256,
			local: ''
		}
	},
	onLoad() {

	},
	onShow() {
		if (uni.getStorageSync('storeList')) {
			this.address = uni.getStorageSync('storeList');
			this.local = uni.getStorageSync('local')
		} else {
			this.getLocation()
		}
	},
	methods: {
		// 重新定位
		regetLocation() {
			console.log(1);
			uni.getSetting({
				success: (res) => {
					console.log(res);
					if (res.authSetting['scope.userLocation'] === true) {

					}
				}
			})
		},
		showInput() {
			this.inputShowed = true
		},
		hideInput() {
			this.inputVal = "";
			this.inputShowed = false;
			uni.hideKeyboard(); //强行隐藏键盘
		},
		clearInput() {
			this.inputVal = ""
		},
		inputTyping(e) {
			this.inputVal = e.detail.value
		},
		// 获取当前位置
		getLocation() {
			uni.getLocation({
				success: (res) => {
					this.getCity(res.latitude, res.longitude) //获取当前所在城市						
				},
				fail: (res) => {
					console.log(res);

				}
			})
		},
		// 获取定位城市
		getCity(latitude, longitude) {
			// 实例化API核心类
			var demo = new QQMapWX({
				key: 'LVHBZ-SOY6W-Z5URP-OC34Q-CLWZH-Y6BCX' // 开发密钥（key）
			});
			demo.reverseGeocoder({
				location: {
					latitude: latitude,
					longitude: longitude
				},
				success: (res) => {
					console.log(res);
					this.city = res.result.ad_info.city;
					let latitude = res.result.location.lat;
					let longitude = res.result.location.lng;
					this.local = res.result.address;
					let params = {
						city: this.city,
						from: latitude + ',' + longitude
					}
					this.$xm.post('/Index/getStoreList', params, (res) => {
						this.address = res.data;
					})
				},
				fail: function(res) {
					console.log(res)

				},
				complete: function(res) {

				}
			});
		},


		choose(e) {
			uni.setStorageSync('store', e);
			uni.navigateBack();
		},
		go(event) {
			const index = Number(event.currentTarget.dataset.id)
			const item = this.address[index];
			const latitude = Number(item.latitude)
			const longitude = Number(item.longitude)
			uni.openLocation({
				name: item.title,
				address: item.address,
				latitude,
				longitude,
				scale: 18
			})
		}
	}
}
